:root {
  --index: calc(1vw + 1vh)
}

@font-face {
  font-family: LatoBlack;
  src: url("./Fonts/Lato-Black.ttf");
}

@font-face {
  font-family: LatoBold;
  src: url("./Fonts/Lato-Bold.ttf");
}

@font-face {
  font-family: LatoLight;
  src: url("./Fonts/Lato-Light.ttf");
}

@font-face {
  font-family: LatoRegular;
  src: url("./Fonts/Lato-Regular.ttf");
}

@font-face {
  font-family: LatoThin;
  src: url("./Fonts/Lato-Thin.ttf");
}

html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-height: 100vh;
  min-width: 100vw;
  scroll-behavior: smooth;
  overflow-x: hidden !important;

  font-family: LatoRegular, sans-serif;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-height: 100vh;
  min-width: 100vw;
  scroll-behavior: smooth;
  overflow-x: hidden !important;

  font-family: LatoRegular, sans-serif;
}

*, *:before, *:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

header, .header-container, .header-upper, footer, .footer-container, .footer-upper {
  display: flex;
  width: 100%;
  height: auto;

  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
}

header {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
}

.header-container, .footer-container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 75%;

  justify-content: center;
  align-items: center;
}

.header-upper, .footer-upper {
  height: auto;
  padding: calc(var(--index) * 1) calc(var(--index) * .25);

  border-bottom: 1px solid rgba(124, 128, 135, 0.14);
}

.header-lower {
  display: flex;
  flex-direction: row;
  height: auto;
  width: 100%;

  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
}

.footer-lower {
  display: flex;
  flex-direction: row;

  width: 100%;

  justify-content: left;
  align-items: start;
}

.footer-element-lower {
  display: flex;
  flex-direction: column;

  justify-content: left;
  align-items: start;
  text-align: left;

  width: 25%;
}

.footer-element-lower:last-child {
  align-items: end;
  text-align: right;
}

.footer-element-lower h3 {
  font-family: LatoRegular, sans-serif !important;
  font-size: calc(var(--index) * 1);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0;
}

.footer-element-lower a {
  color: rgb(115, 122, 128);
  margin: calc(var(--index) * .5) 0;
  text-decoration: none;

  transition-duration: .5s;

  text-align: left !important;
  font-family: LatoRegular, sans-serif !important;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  font-weight: 300;
  letter-spacing: 0;
}

#and-more {
  text-align: left !important;
  font-family: LatoRegular, sans-serif !important;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  font-weight: 700;
  letter-spacing: 0;

  color: rgba(30, 90, 109, 1);
}

.footer-element-lower a:hover {
  text-decoration: underline;
}

.header-element-lower, .footer-element {
  display: flex;
  flex-direction: row;

  justify-content: center;
  align-items: center;
  align-content: center;

  width: 50%;

  margin: calc(var(--index) * .5) 0 0 0;

  padding: calc(var(--index) * .5) calc(var(--index) * 1);
}

.header-element-lower button, .footer-element button {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: calc(var(--index) * 1);
  margin: calc(var(--index) * .5) 0;

  background-color: #ffffff;
  border: none;

  cursor: pointer;

  font-size: calc(var(--index) * .75);
  line-height: 1.7;
  letter-spacing: 0;

  font-weight: 700;

  transition-duration: .5s;
}

.footer-element button {
  padding: 0 calc(var(--index) * .25) !important;
}

.header-element-lower button:hover, .footer-element button:hover {
  color: #ffffff;
}

.footer-element-lower button {
  color: rgb(115, 122, 128) !important;
  margin: calc(var(--index) * .5) 0 !important;
  text-decoration: none !important;
  text-align: left !important;
  font-family: LatoLight, sans-serif !important;
  font-size: calc(var(--index) * .6) !important;
  line-height: 1.7 !important;
  font-weight: 300 !important;
  letter-spacing: 0 !important;

  border: none;
  background: #ffffff;

  cursor: pointer;
}

.footer-element-lower button:hover {
  text-decoration: underline !important;
}

.header-element-lower a, .footer-element a {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: calc(var(--index) * 1);
  margin: calc(var(--index) * .5) 0;

  text-decoration: none;

  background-color: #ffffff;
  color: rgb(48, 49, 57);
  border: none;

  cursor: pointer;

  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * .75);
  line-height: 1.7;
  letter-spacing: 0;

  font-weight: 600;

  transition-duration: .5s;
}

.header-element-lower a:hover, .footer-element a:hover {
  color: #ffffff;
}

.main-btn {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  text-align: center !important;
  margin: calc(var(--index) * .5) 0 !important;

  width: fit-content;

  cursor: pointer !important;

  font-size: calc(var(--index) * .75) !important;
  line-height: 1.7 !important;
  letter-spacing: 0 !important;

  font-weight: 500 !important;
  text-decoration: none !important;

  transition-duration: .5s !important;

  border: 2px solid rgba(30, 90, 109, 1) !important;
  border-radius: calc(var(--index) * .25) !important;
  background-color: rgba(30, 90, 109, 1) !important;
  color: #ffffff !important;
  padding: calc(var(--index) * .5) calc(var(--index) * 1) !important;
}

.main-btn:hover {
  border: 2px solid rgba(30, 90, 109, .8) !important;
  background-color: rgba(30, 90, 109, .8) !important;
}

.header-element, .footer-element {
  display: flex;
  flex-direction: column;

  flex: 1 1 25%;
  max-width: 25%;

  justify-content: center;

  white-space: pre-wrap;
  margin: calc(var(--index) * .5) 0;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  letter-spacing: 0;
}

#foo-el {
  flex-direction: row !important;
}

#header-logo p:first-child, #footer-logo p:first-child {
  white-space: pre-wrap;
  margin: calc(var(--index) * .5) 0 calc(var(--index) * -.25) 0;
  font-size: calc(var(--index) * 1);
  line-height: 1.5;
  letter-spacing: 0;
  font-weight: 400;
}

#header-logo p:nth-child(2), #footer-logo p:nth-child(2) {
  font-size: calc(var(--index) * .6);
  line-height: 1;
  letter-spacing: 0;
  margin: calc(var(--index) * .5) 0;
  color: rgb(157, 165, 168);
}

.header-element:first-child p, .footer-element:first-child p {
  display: flex;
  text-align: left;
}

.footer-element .main-btn {
  padding-left: calc(var(--index) * 1) !important;
  padding-right: calc(var(--index) * 1) !important;
}

#homeScreen {
  min-height: 100vh;
}

#infoScreen {
  min-width: 100vw;
}

#infoScreen section {
  width: 100%;
}

#infoDescription {
  display: flex;
  flex-direction: column;

  text-align: justify !important;
  font-family: LatoRegular, sans-serif !important;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  font-weight: 300;
  letter-spacing: 0;

  padding-right: calc(var(--index) * 5);
}

section {
  display: flex;
  flex-direction: column;
  min-height: auto;

  width: 100%;

  background: #ffffff;

  justify-content: center;
  align-items: center;
}

section:nth-child(2n+1) {
  background: rgba(49, 52, 58, 1);
  color: #ffffff;
}

#first-section {
  height: auto;
  background-size: cover;
  object-fit: cover;
  background-position: 50% 80%;
}

.banner {
  display: flex;
  flex-direction: row;

  position: relative;
  justify-content: space-between;
  align-items: center;

  width: 100%;

  padding: calc(var(--index) * 5) calc(var(--index) * 10);

  color: #ffffff;
}

.banner:before {
  content: '';
  position: absolute;

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background-image: url("./Images/bosch-background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  filter: grayscale(50%) contrast(50%) brightness(60%);
  background-position: 50% 80%;

  z-index: 10;
}

.banner-item {
  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: start;

  width: 50%;
  height: 100%;

  z-index: 11;
}

.banner-item:nth-child(2) {
  justify-content: center;
  align-items: center;
}

.banner-item h1 {
  display: flex;
  flex-direction: column;
  text-align: left;
  font-size: calc(var(--index) * 2);
  line-height: 1.3;
  font-weight: 900;
  letter-spacing: 0;

  margin: calc(var(--index) * .5) 0;
}

.banner-item .focus {
  font-weight: 900;
}

.banner-item img {
  display: flex;
  height: calc(var(--index) * 20);
}

#infoImg img {
  height: calc(var(--index) * 20);
}

.banner-sale {
  width: calc(var(--index) * 17.5);
  display: flex;
  flex-direction: row;

  justify-content: space-evenly;
  align-items: center;

  background: #ffffff;
  border-radius: calc(var(--index) * .25);
  padding: calc(var(--index) * .25);

  transition-duration: .5s;

  transform: translate(calc(var(--index) * 0), calc(var(--index) * -2));
}

.banner-sale svg {
  color: rgba(30, 90, 109, 1);
  width: calc(var(--index) * 2);
  height: calc(var(--index) * 2);

  transition-duration: .5s;
}

.banner-sale svg:hover {
  color: rgba(18, 170, 235, .8);
}

.banner-sale p {
  margin: calc(var(--index) * .5) 0;
}

.banner-sale h6 {
  text-align: left;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * 1);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0;

  margin: 0;

  color: #212121;
}

.banner-sale span {
  text-align: left;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  font-weight: 300;
  letter-spacing: 0;

  color: rgb(157, 165, 168);
}

.banner-sale u {
  text-align: left;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  font-weight: 700;
  letter-spacing: 0;

  color: rgba(30, 90, 109, 1);
}

.description {
  display: flex;
  flex-direction: row;

  justify-content: space-between;
  text-align: left;

  width: 100%;
}

.description-item {
  display: flex;
  width: 45%;
  justify-content: left;
  align-items: center;

  padding: calc(var(--index) * .5) calc(var(--index) * .5);

  margin-bottom: calc(var(--index) * .5);
}

.description-item svg {
  color: rgba(255, 255, 255, 1);

  width: calc(var(--index) * 3);
  height: calc(var(--index) * 3);

  margin-right: calc(var(--index) * .25);

  transition-duration: .5s;
}

.description-item svg:hover {
  opacity: .8;
}

.description-item span {
  display: flex;
  justify-content: center;
  align-items: start;
  text-align: left;
}

.banner-phones {
  margin: calc(var(--index) * .25) 0;
}

.banner-socials {
  display: flex;
  margin: calc(var(--index) * .25) 0;
}

.focus {
  margin: 0 calc(var(--index) * .5);
  color: rgb(224, 4, 32);
  font-weight: 900;
}

#section-phone {
  color: white;
  text-align: left;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * 1);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0;

  text-decoration: none;
}

#section-phone:hover {
  text-decoration: underline;
}

.socials {
  background: rgba(74, 201, 89, 1);

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50%;
  height: calc(var(--index) * 2);
  width: calc(var(--index) * 2);
  margin-right: calc(var(--index) * .5);

  cursor: pointer;

  transition-duration: .5s;
}

.socials:hover {
  background: rgba(74, 201, 89, .8);
}

.socials:nth-child(2) {
  height: calc(var(--index) * 2);
  width: calc(var(--index) * 2);
  background: rgba(0, 136, 204, 1);
  margin-right: calc(var(--index) * .25);
}

.socials:nth-child(2):hover {
  background: rgba(0, 136, 204, .8);
}

#telegram {
  width: calc(var(--index) * 1.25);
  height: calc(var(--index) * 1.25);
}

.products-container {
  max-width: calc(var(--index) * 50);
  display: grid;
  align-items: center;
  justify-content: center;
  align-content: space-between;
  grid-template-columns: 2fr 2fr;
  gap: calc(var(--index) * .25) calc(var(--index) * .25);

  margin: calc(var(--index) * .25) auto;
  padding: calc(var(--index) * 1.75) 0;
}

.product-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;

  align-self: center;
  justify-self: center;

  color: rgb(49, 52, 58);

  width: calc(var(--index) * 25);
  height: calc(var(--index) * 15);

  text-decoration: none;

  padding: calc(var(--index) * .5);

  border-radius: calc(var(--index) * .5);
  background-color: rgba(223, 226, 228, 1);
}

.product-title {
  display: flex;
  width: 100%;
  height: 10%;

  justify-content: left;
  align-items: center;
  align-self: start;

  text-align: left;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * 1.25);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0;
}

.product-title span {
  color: rgb(224, 4, 32);
  margin-left: calc(var(--index) * .25);
}

.product-body {
  display: flex;

  justify-content: space-between;

  width: 100%;
  height: 80%;

  flex-direction: row;
}

.product-models {
  display: flex;
  flex-direction: column;

  text-align: left;

  width: auto;
  height: 100%;

  gap: calc(var(--index) * .75) 0;

  padding: 0 calc(var(--index) * 1);

  font-family: LatoLight, sans-serif;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  font-weight: 300;
  letter-spacing: 0;
}

.product-models strong {
  color: rgba(30, 90, 109, 1);
  font-family: LatoBold, sans-serif;
  font-weight: 700;
}

.product-image {
  display: flex;
  width: 50%;
  height: 100%;

  justify-content: center;
  align-items: center;
}

.product-image img {
  display: flex;

  width: auto;
  height: 100%;
}

.ps-product-models {
  display: flex;
  flex-direction: column;

  width: 50%;
  height: auto;

  margin: calc(var(--index) * 1) auto;

  padding: calc(var(--index) * .5) calc(var(--index) * .5);

  border-radius: calc(var(--index) * .5);
  background-color: rgba(223, 226, 228, 1);
}

.ps-models-title {
  display: flex;
  width: 100%;
  height: 10%;

  justify-content: left;
  align-items: center;
  align-self: start;

  text-align: left;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * 1.25);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0;
}

.ps-models-title span {
  color: rgb(224, 4, 32);
  margin-left: calc(var(--index) * .25);
}

.ps-models-body {
  display: flex;
  flex-direction: row;
}

.ps-models-body-item:first-child {
  display: grid;
  grid-template-columns: 2fr 2fr;

  grid-row-gap: calc(var(--index) * .75);

  width: 50%;

  padding: calc(var(--index) * .5) calc(var(--index) * .5);
}

.ps-models-body-item:nth-child(2) {
  display: flex;

  justify-content: center;
  align-items: center;

  width: 50%;

  padding: calc(var(--index) * .5) calc(var(--index) * .5);
}

.ps-models-body-item img {
  display: flex;

  width: auto;
  height: 100%;
}

.price-list {
  display: flex;
  flex-direction: column;

  width: 100%;

  color: #ffffff;

  padding: calc(var(--index) * 2) 0;

  justify-content: center;
  align-items: center;
}

.price-list h1 {
  margin: calc(var(--index) * 1) 0;

  text-align: center;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * 2);
  line-height: 1.3;
  font-weight: 400;
  letter-spacing: 0;
}

.price-list h3 {
  margin: 0 0 calc(var(--index) * .5) 0;

  text-align: center;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * 1);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0;

  color: rgb(13, 204, 222);
}

.price-list-container {
  display: flex;
  flex-direction: column;

  width: 60%;

  margin: 0;
  padding: 0;
}

.price-title {
  display: flex;

  border-radius: calc(var(--index) * .25);

  background-color: rgba(115, 122, 128, 0.2);
}

.price-title strong {
  width: 50%;
  padding: calc(var(--index) * .75) calc(var(--index) * .5);

  text-align: left;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * .65);
  line-height: 1.7;
  font-weight: 700;
  letter-spacing: 0;
}

.service-item-container {
  display: flex;
  flex-direction: column;

  border-radius: calc(var(--index) * .5);
  border-width: 0 4px 4px 4px;
  border-style: solid;
  border-color: rgba(30, 90, 109, 1);

}

.service-item {
  display: flex;

  padding: calc(var(--index) * .75) calc(var(--index) * .5);
}

.service-name, .service-price {
  width: 50%;

  font-size: calc(var(--index) * .75);
}

.service-price {
  padding: calc(var(--index) * .5);
}

.examples {
  display: flex;
  flex-direction: column;
  width: 60%;
  height: auto;

  justify-content: center;
  align-items: center;
}

.after-price {
  display: flex;
  flex-direction: row;

  justify-content: space-evenly;
  /*align-items: center;*/

  width: 100%;
  height: auto;

  padding: calc(var(--index) * 1) calc(var(--index) * .25);

  color: #ffffff;
}

.after-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 45%;
  height: fit-content;
}

.after-item h4 {
  text-align: center;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * 1);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0;

  margin-bottom: calc(var(--index) * 1);
}

.after-item p {
  text-align: center;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  font-weight: 300;
  letter-spacing: 0;

  margin-bottom: calc(var(--index) * .75);
}

.after-item input {
  border: none;
  width: 100%;
  height: calc(var(--index) * 2);

  padding: calc(var(--index) * .75);
  background: #ffffff;
  color: rgba(223, 226, 228, 1);

  font-size: calc(var(--index) * .6);
  font-weight: 500;

  margin-bottom: calc(var(--index) * 0);
}

.after-item img {
  width: 100%;
  height: calc(var(--index) * 10);
  object-fit: cover;
  object-position: center 80%;
}

.examples h1 {
  text-align: center;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * 1.75);
  line-height: 1.3;
  font-weight: 400;
  letter-spacing: 0;
}

.examples h3 {
  text-align: center;
  font-family: LatoBold, sans-serif;
  font-size: calc(var(--index) * .75);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0;
}

.examples-container {
  display: grid;
  grid-template-columns: 22.5% 22.5% 22.5% 22.5%;

  justify-content: space-between;

  width: 100%;

  /*grid-column-gap: calc(var(--index) * .5);*/
}

.example-item {
  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;
}

.example-item h4 {
  text-align: center;
  font-family: LatoLight, sans-serif;
  font-size: calc(var(--index) * .6);
  line-height: 1.7;
  font-weight: 100;
  letter-spacing: 0;
}

.models-and-types {
  display: flex;
  flex-direction: column;

  width: 100%;

  justify-content: center;
  align-items: center;
}

.models-and-types h1 {
  margin: calc(var(--index) * 1) 0;

